@font-face {
  font-family: 'Hauracherell-NC';
  src: url('Hauracherell-NC.woff') format('woff'),
    url('Hauracherell-NC.woff2') format('woff2'),
    url('Hauracherell-NC.svg#Hauracherell-NC') format('svg'),
    url('Hauracherell-NC.eot'),
    url('Hauracherell-NC.eot?#iefix') format('embedded-opentype'),
    url('Hauracherell-NC.ttf') format('truetype');

  font-weight: normal;
  font-style: normal;
  font-display: swap;
}

:root {

  --light-blue: rgba(207, 234, 243, 1);
  --med-blue: #5DC1E8;
  --dark-blue: rgb(62, 94, 148);
  --dark-blue-2: rgba(3, 50, 124, 0.9);
  --orange: #ff9838;
  --orange-yellow: #FFA024;
  --dark-yellow: #F7C92E;
  --light-yellow: #F8F7D9;
  --card-center-yellow: #F8D77B;
  --card-center-purple: #C7BEDD;
  --red: #ff444e;
  --royal-red: #E13C3F;
  --dark-teal: #0C4E66;
  --light-purple: #DEDAEB;
  --dark-purple: #53318F;
  --green: #5BB249;

  --ff-primary: 'Zen Maru Gothic', sans-serif;
  --ff-heading: 'Hauracherell-NC', sans-serif;
}

body {
  margin: 0;
  font-family: var(--ff-primary);
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  font-size: 1.5em;
  text-align: center;
  color: #fff;
  min-height: 100vh;
  background-color: var(--med-blue);
  background-image: linear-gradient(315deg, var(--med-blue) 0%, var(--light-blue) 74%);
}

