.card {
    position: relative;
}

.card img {
    border-radius: 6px;
    width: 100%;
    display: block;
    /* border: 2px solid #F7C92E; */
    box-shadow: -2px 3px 3px 3px rgb(0 0 0 / 30%);
}

/* front of card - the character */
.card .front {
    transform: rotateY(90deg);
    transition: all ease-in 0.2s;
    position: absolute;
    transform-style: preserve-3d;
}

.flipped .front {
    transform: rotateY(0deg);
    transition-delay: 0.2s;
}

/* back of card */
.card .back {
    transition: all ease-in 0.2s;
    transition-delay: 0.2s;  
    transform-style: preserve-3d;
    cursor: pointer;
}

.flipped .back {
    transform: rotateY(90deg);
    transition-delay: 0s;
}

